import { createStore } from "vuex";

export default createStore({
    state: {
        ref: {},
        popup: {
            name: '',
            isOpen: false,
        },
        userInfo: {
        },
        resultPopData: {

        }
    },
    mutations: {

        popupControl(state, option) {
            state.popup.isOpen = option.isOpen;
            state.popup.name = option.name;
        }
    },

    ref: {

    }
});