<template>
  <div class="pd3 border-box">
    <article class="pd15 bg-fff-0_6 border-eee radius20">
      <h4 class="mp0 mg-b10">내정보</h4>
      <div>
        <div>
          <span>{{$store.state.userInfo.nickname}}</span>
          <span>({{$store.state.userInfo.tel}})</span>
        </div>
        <h3 class="mg-b0">
          <strong>{{$store.state.userInfo.point_now_with_comma}}원</strong>
          <button class="pd10 border0 outline0 font16 mg-l10 radius50 w100" style="background:#e7c13c;"
          @click="getOutputRequestBox(1);">
            <strong>출금요청</strong>
          </button>
        </h3>
      </div>
    </article>
    <article class="pd15 bg-fff-0_6 mg-t10 border-eee radius20" v-if="state.outputBtnOn">
      <ul class="list-style-none mp0">
        <li class="mg-b5">
          <label class="input-style1 w100p bg-fff">
              <input type="text" placeholder=" " />
              <strong class="color-bbb">예금주</strong>
              <span class="radius50"></span>
          </label>
        </li>
        <li class="mg-b5">
          <label class="input-style1 w100p bg-fff">
              <input type="text" placeholder=" " />
              <strong class="color-bbb">은행명</strong>
              <span class="radius50"></span>
          </label>
        </li>
        <li class="mg-b5">
          <label class="input-style1 w100p bg-fff">
              <input type="text" placeholder=" " />
              <strong class="color-bbb">계좌번호</strong>
              <span class="radius50"></span>
          </label>
        </li>
        <li>
          <label class="input-style1 w100p bg-fff">
              <input type="number" placeholder=" " class="txt-r" />
              <strong class="color-bbb">금액</strong>
              <span class="radius50"></span>
          </label>
        </li>
      </ul>
      <div class="mg-t10 txt-c">
        <button class="w100 pd10 border0 outline0 font16 mg-r5 radius50" style="background:#e7c13c;"><strong>요청</strong></button>
        <button class="w100 pd10 border0 outline0 font16 radius50" @click="getOutputRequestBox(0);"><strong>취소</strong></button>
      </div>
    </article>
    <article class="pd15 bg-fff-0_6 mg-t10 border-eee radius20">
      <div class="flex flex-row border-b-eee pd-v20 border-v-eee">
        <div class="flex flex-col flex1 txt-c">
          <div class="txt-c mg-b10 color-green"><strong>정답</strong></div>
          <div class="txt-c">{{$store.state.userInfo.correct_answer_cnt_with_comma}}</div>
        </div>
        <div class="flex flex-col flex1 border-l-eee border-r-eee">
          <div class="txt-c mg-b10 color-red"><strong>오답</strong></div>
          <div class="txt-c">{{$store.state.userInfo.incorrect_answer_cnt_with_comma}}</div>
        </div>
        <div class="flex flex-col flex1">
          <div class="txt-c mg-b10"><strong>총 문제</strong></div>
          <div class="txt-c">{{$store.state.userInfo.answer_cnt_with_comma}}</div>
        </div>
      </div>
      <!-- <h4 class="mp0 mg-v10">최근 퀴즈</h4> -->
    </article>
  </div>
</template>
<script>
import { onMounted } from "@vue/runtime-core";
import { reactive } from "@vue/reactivity";
import node from '../config/backEnd.js';
import { useStore } from "vuex";
import axios from 'axios';
export default {
  components: {
  },
  setup() {
    const store = useStore();
    const state = reactive({
      outputBtnOn: false
    })

    const getOutputRequestBox = (boolean) => {
      state.outputBtnOn = boolean ? true : false;
    }

    const getUserInfo = (seq_m) => {
      // console.log(seq_m);
      axios.post(`http://${node.nodeUrl}/tmpLogin`, {seq: seq_m}).then((res) => {
        store.state.userInfo = res.data[0];
        console.log(store.state.userInfo);
      });
    }

    onMounted(() => {
    });

    return {
      //var
      state,
      //var

      //ref
      //ref

      //method
      getOutputRequestBox,
      getUserInfo,
      //method
    };
  },
};
</script>